<template>
  <div class="container row">
    <!-- Action Buttons -->
    <!-- <div v-show="!showTools && !editMode" class="actionButtons col-12"> -->
    <div class="actionButtons col-12">
      <div class="left">
        <q-btn
          class="q-mr-sm"
          flat
          color="white"
          size="sm"
          icon="stay_current_landscape"
          @click="onChangeOrientation('L')"
        />
        <q-btn
          class="q-mr-sm"
          flat
          color="white"
          size="sm"
          icon="crop_square"
          @click="onChangeOrientation('S')"
        />
        <q-btn
          class="q-mr-sm"
          flat
          color="white"
          size="sm"
          icon="stay_current_portrait"
          @click="onChangeOrientation('P')"
        />
      </div>
      <div class="right">
        <q-btn
          round
          style="background: #ffc107; color: white"
          class="q-mr-lg"
          icon="qr_code"
          size="md"
        />
        <q-btn
          style="background: #ffc107; color: white"
          label="저장"
          size="md"
          @click="onClickSaveToImage"
        />
      </div>
    </div>
    <!-- 에디터 -->
    <div v-show="isReady" class="editor col-xs-12 col-md-6">
      <!-- <div class="top">Preview</div> -->
      <div class="content">
        <div
          id="canvas-container"
          :class="[{ landscape: isLandscape }, { vertical: !isLandscape }]"
        >
          <!-- <canvas class="pinch-zoom" ref="canvasRef"></canvas> -->
          <canvas ref="canvasRef"></canvas>
        </div>
      </div>
      <div class="bottom">
        <Toolbar
          v-show="showTools"
          :activeObject="activeObject"
          @emit_changeColor="changeColor"
          @emit_changeFont="changeFont"
          @emit_changeFontSize="changeFontSize"
        ></Toolbar>
      </div>
    </div>
    <!-- 레이어 리스트 -->
    <div class="layer col-xs-12 col-md-6">
      <Layer
        v-if="isReady"
        :originImageResources="originImageResources"
        :componentList="componentList"
        :activeObjectIndex="activeObjectIndex"
        @emit_selectObject="selectObject"
        @emit_changeTextBox="changeTextBox"
        @emit_changeImage="changeImage"
        @emit_resetImage="resetImage"
      ></Layer>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onBeforeUnmount } from "vue";
import { fabric } from "fabric";
import "fabric-history";
import Toolbar from "@/components/Toolbar.vue";
import Layer from "@/components/Layer.vue";
import MOCK_3_LANDSCAPE from "/public/meta/mock_3_landscape.json";
import MOCK_3_VERTICAL from "/public/meta/mock_3_vertical.json";
import MOCK_3_SQURE from "/public/meta/mock_3_squre.json";
import PinchZoom from "pinch-zoom-js";
import * as _ from "lodash";

export default {
  name: "Editor",
  components: {
    Toolbar,
    Layer,
  },
  setup() {
    const SCALE_DOWN_LEVEL = 2.62;
    let META_DATA = ref(MOCK_3_LANDSCAPE.providerMetadata);
    let canvasContext = null;
    let canvasRef = ref(null);
    let activeObject = ref(null);
    let activeObjectIndex = ref(-1);
    let canvasObjects = ref([]);
    let showTools = ref(false);
    let editMode = ref(false);
    let isReady = ref(false);
    let originImageResources = ref([]);
    let componentList = computed(() => canvasObjects.value);
    const isLandscape = computed(() =>
      META_DATA.value?.attributes?.orientation === "Portrait" ? false : true
    );
    let scaleDownPoint = {
      beforeWidth: 0,
      widthList: [],
    };

    onMounted(() => {
      canvasContext = createCanvas();
      loadResource();
      addEventListener();
    });

    onBeforeUnmount(() => {
      window.removeEventListener("resize", resizeHandler);
    });

    /**
     * Canvas 생성
     */
    const createCanvas = () => {
      let width = isLandscape.value
        ? (1920 / SCALE_DOWN_LEVEL).toString()
        : (1080 / SCALE_DOWN_LEVEL).toString();
      let height = isLandscape.value
        ? (1080 / SCALE_DOWN_LEVEL).toString()
        : (1920 / SCALE_DOWN_LEVEL).toString();

      return new fabric.Canvas(canvasRef.value, {
        preserveObjectStacking: true, // 오브젝트를 선택해도 layer를 유지하는 옵션
        stopContextMenu: true,
        isDrawingMode: false,
        width: width,
        height: height,
        centeredScaling: true,
        lockUniScaling: true,
        allowTouchScrolling: false,
      });
    };

    /**
     * 템플릿 데이터 로드
     */
    const loadResource = async () => {
      let svgResource = convertToSVGResource();

      // Image
      if (svgResource.imageResources) {
        await loadImageResource(svgResource.imageResources).then((res) => {
          drawImageResources(res);
        });
      }
      // Text
      if (svgResource.textResources) {
        await drawTextResources(svgResource.textResources);
      }

      // Lock Objects
      let objects = canvasContext.getObjects();
      objects.forEach((item) => {
        item.selectable = false;
        item.evented = false;
      });
      canvasObjects.value = objects;

      // Move center
      let selection = new fabric.ActiveSelection(objects, {
        canvas: canvasContext,
      });
      selection.center();
      selection.destroy();

      // Pinchzoom
      // applyPinchZoom();

      // Canvas 랜더링 완료
      isReady.value = true;
    };

    const applyPinchZoom = () => {
      const el = document.querySelector(".pinch-zoom");
      new PinchZoom(el, {
        minZoom: 0.8,
        maxZoom: 10,
        tapZoomFactor: 4,
        draggableUnzoomed: false,
        onDoubleTap: (pinch, event) => {
          event.preventDefault();
        },
      });
    };

    /**
     * 이미지 리소스 Canvas에 포함
     *
     * @param {*} imageResources
     */
    const drawImageResources = (imageResources) => {
      imageResources.forEach((image) => {
        let layout = image.layout;
        let imgObj = new fabric.Image(image);

        imgObj.set({
          originIndex: image.originIndex,
          crossOrigin: "Anonymous",
          left: layout.origin.x / SCALE_DOWN_LEVEL,
          top: layout.origin.y / SCALE_DOWN_LEVEL,
          width: layout.size.width / SCALE_DOWN_LEVEL,
          height: layout.size.height / SCALE_DOWN_LEVEL,
        });
        canvasContext.add(imgObj);
        canvasContext.renderAll();
        originImageResources.value.push(_.cloneDeep(imgObj));
      });
    };

    /**
     * 이미지 preload
     *
     * @param {*} imageResources
     */
    const loadImageResource = (imageResources) => {
      let imageObjectList = [];
      let imageLoadedCount = 0;

      return new Promise((resolve) => {
        imageResources.forEach((image, index) => {
          let layout = image.layoutAttributes;
          let component = image.componentAttributes;
          let imageObj = new Image();
          imageObj.originIndex = image.originIndex;
          imageObj.index = index;
          imageObj.src = component.attributes.location;
          imageObj.layout = layout;
          imageObjectList.push(imageObj);

          // image preload
          imageObj.onload = () => {
            imageLoadedCount += 1;
            if (imageLoadedCount === imageResources.length) {
              resolve(imageObjectList);
            }
          };
        });
      });
    };

    /**
     * 텍스트 리소스 Canvas에 포함
     *
     * @param {*} textResources
     */
    const drawTextResources = (textResources) => {
      textResources.forEach((item) => {
        let component = item.componentAttributes;
        let layout = item.layoutAttributes;
        if (component && layout) {
          let componentAttr = component.attributes;
          let textBox = new fabric.Textbox(componentAttr.content, {
            originIndex: item.originIndex,
            fill: componentAttr.color,
            textAlign: layout["text-align"],
            fontFamily: layout.font,
            fontSize: Math.round(layout.fontsize / SCALE_DOWN_LEVEL),
            width: Math.round(layout.size.width / SCALE_DOWN_LEVEL),
            height: Math.round(layout.size.height / SCALE_DOWN_LEVEL),
            left: Math.round(layout.origin.x / SCALE_DOWN_LEVEL),
            top: Math.round(layout.origin.y / SCALE_DOWN_LEVEL),
            lockMovementX: true,
            lockMovementY: true,
            lockScalingX: true,
            lockScalingY: true,
            centeredScaling: true,
            splitByGrapheme: true,
            breakWords: true,
          });

          canvasContext.add(textBox);
          canvasContext.renderAll();
        }
      });
    };

    /**
     * 각 리소스 타입별 리소스 변환
     */
    const convertToSVGResource = () => {
      let svgResource = {};
      let imageResources = [];
      let textResources = [];
      let shapeResources = [];
      let providerMetadata = META_DATA.value;

      if (providerMetadata) {
        let layout = providerMetadata.layout;
        let components = providerMetadata.components;

        if (layout && components) {
          // Layout loop
          layout.forEach((item, index) => {
            let layoutAttributes = item.attributes;
            let componentAttributes = components.find(
              (comp) => comp.id === item.id
            );
            let attributes = {
              originIndex: index,
              layoutAttributes,
              componentAttributes,
            };

            if (
              item.subtype === "Background" ||
              item.subtype === "Image" ||
              item.subtype === "Sticker"
            ) {
              imageResources.push(attributes);
            } else if (item.subtype === "TextBox") {
              textResources.push(attributes);
            } else if (item.subtype === "Shape") {
              shapeResources.push(attributes);
            }
          });
          svgResource.imageResources = imageResources;
          svgResource.textResources = textResources;
        }
      }
      return svgResource;
    };

    const handleSelectObject = (item) => {
      let object = item.selected[0];
      let originIndex = object.originIndex;
      activeObject.value = object;
      showTools.value = object.text ? true : false;
      activeObjectIndex.value = originIndex;

      // if (!object.text) {
      //   showTools.value = false;
      // }
    };

    const onMouseWheel = (options) => {
      let delta = options.e.deltaY;
      let zoom = canvasContext.getZoom();
      if (delta > 0) {
        zoom -= 0.1;
      } else {
        zoom += 0.1;
      }
      if (zoom > 20) zoom = 10;
      if (zoom < 0.5) zoom = 0.5;
      canvasContext.zoomToPoint(
        { x: options.e.offsetX, y: options.e.offsetY },
        zoom
      );
      options.e.preventDefault();
      options.e.stopPropagation();
    };

    const onMouseDblclick = () => {
      canvasContext.setViewportTransform([1, 0, 0, 1, 0, 0]);
    };

    const clearObject = () => {
      activeObjectIndex.value = -1;
      activeObject.value = null;
      showTools.value = false;
    };

    const changeColor = (color) => {
      canvasContext.getActiveObject().set("fill", color);
      canvasContext.renderAll();
    };

    const changeFont = (font) => {
      canvasContext.getActiveObject().set("fontFamily", font);
      canvasContext.renderAll();
    };

    const changeFontSize = (fontSize) => {
      canvasContext
        .getActiveObject()
        .set("fontSize", Math.round(fontSize / SCALE_DOWN_LEVEL));
      canvasContext.renderAll();
    };

    const selectObject = (objectIndex) => {
      canvasContext.setActiveObject(canvasContext.item(objectIndex));
      canvasContext.renderAll();
    };

    const changeTextBox = (change) => {
      canvasContext.fire("text:changed", {
        target: canvasContext.getActiveObject(),
      });
    };

    const changeImage = (change) => {
      let originObject = canvasContext.item(activeObjectIndex.value);
      let originBoundingRect = originObject.getBoundingRect();
      console.log(originBoundingRect);

      activeObject.value.setSrc(change.base64Image, () => {
        activeObject.value.left = originBoundingRect.left;
        activeObject.value.top = originBoundingRect.top;
        activeObject.value.width = originBoundingRect.width;
        activeObject.value.height = originBoundingRect.height;
      });
      activeObject.value.origin = originObject;
      canvasContext.renderAll();
    };

    const resetImage = (image) => {
      let object = canvasContext.item(activeObjectIndex.value);
      object.set(image);
      canvasContext.renderAll();

      componentList.value[activeObjectIndex.value].changeMode = false;
      activeObjectIndex.value = -1;
    };

    const resizeHandler = () => {
      let prev_width = canvasContext.width;
      canvasContext.getObjects().forEach((e) => {
        e.set({
          left:
            (e.getBoundingRect().left * canvasContext.width) / prev_width +
            (e.left - e.getBoundingRect().left),
        });
        e.setCoords();
      });
      canvasContext.calcOffset();
      canvasContext.renderAll();
    };

    const addEventListener = () => {
      // Canvas 이벤트 => 오브젝트 선택
      canvasContext.on({
        "selection:updated": handleSelectObject,
        "selection:created": handleSelectObject,
      });
      // Canvas 이벤트 => 오브젝트 선택 해제
      canvasContext.on("selection:cleared", clearObject);

      // canvasContext.on("text:changed", (e) => {
      //   console.log(e);
      // });

      // Canvas wheel 이벤트
      canvasContext.on("mouse:wheel", onMouseWheel);

      // Canvas Doubleclick 이벤트
      canvasContext.on("mouse:dblclick", onMouseDblclick);

      // Textbox Chnage 이벤트
      canvasContext.on("text:changed", onChangeTextbox);

      // Window resize 이벤트
      // window.addEventListener("resize", resizeHandler);

      // Window keyup 이벤트
      // addKeyupEventListener();
    };

    /**
     * 오브젝트 영역에 벗어나면 fontSize 조정
     *
     * @param {*} opt
     */
    const onChangeTextbox = (opt) => {
      let target = opt.target;
      let standardWidth = target.width - 20;
      let currentWidth = target.calcTextWidth();
      let textLength = target.text?.length || 0;
      let findIndex = scaleDownPoint.widthList.findIndex(
        (item) => item === textLength
      );
      const minimumFontSize = 6;

      if (textLength < scaleDownPoint.widthList[0]) {
        target.fontSize += scaleDownPoint.widthList.length;
        scaleDownPoint.widthList = [];
        return;
      }

      if (findIndex > -1) {
        target.fontSize += 1;
        scaleDownPoint.widthList.splice(findIndex, 1);
      } else {
        if (
          standardWidth <= currentWidth &&
          textLength > scaleDownPoint.beforeWidth &&
          minimumFontSize <= target.fontSize
        ) {
          scaleDownPoint.widthList.push(textLength);
          target.fontSize -= 1;
        }
      }
      scaleDownPoint.beforeWidth = textLength;
    };

    const onClickSaveToImage = () => {
      let url = canvasContext.toDataURL({
        format: "jpeg",
        quality: 1,
      });
      console.log(url);
    };

    const onChangeOrientation = (type) => {
      isReady.value = false;
      canvasContext.getObjects().forEach((item) => {
        canvasContext.remove(item);
      });

      if (type === "L") {
        META_DATA.value = MOCK_3_LANDSCAPE.providerMetadata;
      } else if (type === "S") {
        META_DATA.value = MOCK_3_SQURE.providerMetadata;
      } else if (type === "P") {
        META_DATA.value = MOCK_3_VERTICAL.providerMetadata;
      }
      canvasContext = createCanvas();
      loadResource();
      addEventListener();
      originImageResources.value = [];
    };

    return {
      isLandscape,
      canvasRef,
      showTools,
      editMode,
      activeObject,
      activeObjectIndex,
      changeColor,
      changeFont,
      changeFontSize,
      componentList,
      originImageResources,
      isReady,
      selectObject,
      changeTextBox,
      changeImage,
      resetImage,
      onClickSaveToImage,
      onChangeOrientation,
    };
  },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  width: 100%;
  border-bottom: 1px solid #d6d6d6;
}
.actionButtons {
  display: flex;
  justify-content: space-between;
  padding: 6px 12px;
  background-color: #444;
  .left {
    align-self: center;
  }
  .right {
    align-self: center;
  }
}
.editor {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #e0e0e0;
}
.top {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 30px;
  color: #fff;
  background-color: #333;
}
.content {
  border-right: 1px solid #d6d6d6;
}
#canvas-container.landscape {
  display: flex;
  justify-content: center;
  ::v-deep(.canvas-container) {
    width: 100% !important;
    // max-width: 480px;
    height: auto !important;
    aspect-ratio: 16 / 9 !important;

    canvas {
      width: 100% !important;
      // max-width: 480px;
      height: auto !important;
      aspect-ratio: 16 / 9 !important;
    }
  }
}
#canvas-container.vertical {
  display: flex;
  justify-content: center;
  ::v-deep(.canvas-container) {
    width: 100% !important;
    // max-width: 180px;
    max-width: 240px;
    height: auto !important;
    aspect-ratio: 9 / 16 !important;

    canvas {
      width: 100% !important;
      // max-width: 180px;
      max-width: 240px;
      height: auto !important;
      aspect-ratio: 9 / 16 !important;
    }
  }
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #444;
  padding: 0 15px;
}

.layer {
  padding-bottom: 12px;
}

::v-deep(.pinch-zoom-container) {
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  bottom: 0 !important;
  right: 0 !important;
  width: 100% !important;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  height: 100% !important;
}
</style>
