<template>
  <q-dialog ref="dialogRef" persistent>
    <q-icon
      name="close"
      class="close all-pointer-events"
      color="white"
      @click="onClickCloseBtn"
    ></q-icon>
    <q-card class="q-dialog-plugin">
      <cropper
        ref="cropperRef"
        class="cropper"
        image-class="cropper__image"
        image-restriction="fill-area"
        default-boundaries="fill"
        :src="imagePath"
        :transitions="true"
        :autoZoom="true"
        :stencil-props="{
          // aspectRatio: 16 / 9,
          class: 'cropper-stencil',
          previewClass: 'cropper-stencil__preview',
          draggingClass: 'cropper-stencil--dragging',
          handlersClasses: {
            default: 'cropper-handler',
            eastNorth: 'cropper-handler--east-north',
            westNorth: 'cropper-handler--west-north',
            eastSouth: 'cropper-handler--east-south',
            westSouth: 'cropper-handler--west-south',
          },
        }"
        @change="change"
      ></cropper>

      <!-- <q-card-actions class="footer" align="right">
        <q-btn
          style="background: #ffc107; color: white"
          size="md"
          label="저장"
          @click="onClickSaveCropImage"
        />
      </q-card-actions> -->
    </q-card>
    <q-card-actions class="footer" align="right">
      <q-btn
        style="background: #ffc107; color: white"
        size="md"
        label="저장"
        @click="onClickSaveCropImage"
      />
    </q-card-actions>
  </q-dialog>
</template>

<script>
import { ref, computed, watch } from "vue";
import { useDialogPluginComponent } from "quasar";
import { Cropper } from "vue-advanced-cropper";
import "vue-advanced-cropper/dist/style.css";

export default {
  name: "ImageCropDialog",
  components: { Cropper },
  props: {
    imagePath: String,
  },
  emits: [
    // REQUIRED; need to specify some events that your
    // component will emit through useDialogPluginComponent()
    ...useDialogPluginComponent.emits,
  ],
  setup(props, { emit }) {
    const { dialogRef, onDialogOK, onDialogHide, onDialogCancel } =
      useDialogPluginComponent();
    const cropperRef = ref(null);

    const onLoadImage = () => {};

    const onClickCloseBtn = () => {
      hide();
    };

    const change = ({ coordinates, canvas }) => {
      console.log(coordinates, canvas);
    };

    const onClickSaveCropImage = () => {
      const result = cropperRef.value.getResult();
      const cropImageSrc = result.canvas.toDataURL({
        format: "jpeg",
        quality: 1,
      });
      onDialogOK({
        cropImage: cropImageSrc,
      });
    };

    const hide = () => {
      emit("hide");
    };

    return {
      dialogRef,
      cropperRef,
      onDialogCancel,
      onDialogHide,
      onClickCloseBtn,
      onLoadImage,
      change,
      onClickSaveCropImage,
    };
  },
};
</script>

<style lang="scss" scoped>
.q-dialog-plugin {
  background: none;
  box-shadow: none;
  width: 100%;
  margin-top: 55px;
  overflow: hidden;
  padding: 16px;
  height: 75vh;

  ::v-deep(.q-img__container) {
    // top: 50%;
    // left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    overflow: hidden;
    width: 100%;
    height: 100%;
    text-align: center;
  }

  ::v-deep(.cropper) {
    ::v-deep(&__image) {
      opacity: 1;
    }
  }

  ::v-deep(.cropper-stencil) {
    ::v-deep(&__preview) {
      &:after,
      &:before {
        content: "";
        opacity: 0;
        transition: opacity 0.25s;
        position: absolute;
        pointer-events: none;
        z-index: 1;
      }

      &:after {
        border-left: solid 1px white;
        border-right: solid 1px white;
        width: 33%;
        height: 100%;
        transform: translateX(-50%);
        left: 50%;
        top: 0;
      }

      &:before {
        border-top: solid 1px white;
        border-bottom: solid 1px white;
        height: 33%;
        width: 100%;
        transform: translateY(-50%);
        top: 50%;
        left: 0;
      }
    }

    ::v-deep(&--dragging) {
      .cropper-stencil__preview {
        &:after,
        &:before {
          opacity: 0.4;
        }
      }
    }
  }

  ::v-deep(.cropper-line) {
    border-color: rgba(white, 0.8);
  }

  ::v-deep(.cropper-handler) {
    display: block;
    opacity: 0.7;
    position: relative;
    flex-shrink: 0;
    transition: opacity 0.5s;
    border: none;
    background: white;
    top: auto;
    left: auto;
    height: 4px;
    width: 4px;
    $length: 16px;

    ::v-deep(&--west-north, &--east-south, &--west-south, &--east-north) {
      display: block;
      height: $length;
      width: $length;
      background: none;
    }
    ::v-deep(&--west-north) {
      border-left: solid 2px white;
      border-top: solid 2px white;
      top: $length / 2 - 1px;
      left: $length / 2 - 1px;
    }
    ::v-deep(&--east-south) {
      border-right: solid 2px white;
      border-bottom: solid 2px white;
      top: -$length / 2 + 1px;
      left: -$length / 2 + 1px;
    }

    ::v-deep(&--west-south) {
      border-left: solid 2px white;
      border-bottom: solid 2px white;
      top: -$length / 2 + 1px;
      left: $length / 2 - 1px;
    }
    ::v-deep(&--east-north) {
      border-right: solid 2px white;
      border-top: solid 2px white;
      top: $length / 2 - 1px;
      left: -$length / 2 + 1px;
    }

    ::v-deep(&--hover) {
      opacity: 1;
    }
  }
}

.close {
  position: absolute;
  top: 55px;
  left: 0;
  font-size: 24px;
  padding: 30px 15px;
  z-index: 9999;
  cursor: pointer;
}

.footer {
  background-color: #444;
}
</style>
