<template>
  <div class="tools row justify-between">
    <!-- 텍스트 Color 변경 -->
    <div class="col-6 textColor">
      <q-input filled v-model="color" class="colorPicker">
        <template v-slot:append>
          <q-icon name="colorize" class="cursor-pointer">
            <q-popup-proxy
              cover
              transition-show="scale"
              transition-hide="scale"
            >
              <q-color
                v-model="color"
                no-header-tabs
                @update:model-value="onChangeColor"
              />
            </q-popup-proxy>
          </q-icon>
        </template>
      </q-input>
    </div>

    <!-- 폰트 변경 -->
    <div class="col-6 textFont">
      <!-- <div id="font-picker"></div> -->
      <q-select
        v-model="fontFamily"
        :options="fontFamilyList"
        bg-color="white"
        dense
        square
        filled
        borderless
        @update:model-value="onChangeFontFamily"
      />
    </div>

    <!-- 폰트 사이즈 변경 -->
    <div class="col-12 textFontSize">
      <q-slider
        v-model="fontSize"
        :marker-labels="markerLabels"
        :min="5"
        :max="200"
        class="q-mt-sm"
        track-color="white"
        @update:model-value="onChangeFontSize"
      />
    </div>
  </div>
</template>

<script>
import { ref, watch, onMounted } from "vue";
import FontPicker from "font-picker";

export default {
  name: "Toolbar",
  emits: ["emit_changeColor", "emit_changeFont", "emit_changeFontSize"],
  props: {
    activeObject: Object,
  },
  setup(props, { emit }) {
    const GOOGLE_FONTS_API_KEY = "AIzaSyAzF9jcH2VStLPUl6JDvj3yuipVM20Os6A";
    const color = ref("#000000");
    const fontSize = ref(0);
    const markerLabels = ref([5, 50, 100, 150, 200]);
    const fontFamilyList = [
      "Arial",
      "Arial Black",
      "Impact",
      "Tahoma",
      "Times New Roman",
    ];
    const fontFamily = ref("Arial");

    watch(props, ({ activeObject }) => {
      if (activeObject) {
        fontSize.value = Math.round(activeObject.fontSize * 2.62);
      }
    });

    onMounted(() => {});

    const onChangeColor = () => {
      emit("emit_changeColor", color.value);
    };

    const onChangeFontSize = () => {
      emit("emit_changeFontSize", fontSize.value);
    };

    const onChangeFontFamily = () => {
      emit("emit_changeFont", fontFamily.value);
    };

    return {
      color,
      fontSize,
      fontFamilyList,
      fontFamily,
      markerLabels,
      onChangeColor,
      onChangeFontSize,
      onChangeFontFamily,
    };
  },
};
</script>

<style lang="scss" scoped>
.tools {
  display: flex;
  width: 100%;
  padding: 8px 16px;
  align-items: center;
}
.colorPicker {
  max-width: 120px;
  background-color: #fff;

  ::v-deep(.q-field__control) {
    height: 40px !important;

    .q-field__append {
      height: 40px !important;
    }
  }
}

.textFont {
  display: flex;
  justify-content: end;

  #font-picker {
    width: auto;
  }
}

.textFontSize {
  margin-left: auto;
  width: 300px;

  ::v-deep(.q-slider__marker-labels-container) {
    color: #fff;
  }
}
</style>
